<template>
 <!-- Sidebar -->
<aside class="col-md-3 sidebar left-sidebar">

<!-- Categories -->
<div class="widget categories">
  <h3 class="widget-title uppercase">Categories</h3>
  <ul class="list-no-dividers">
      <!--<li v-for='(category, index) in catData' key='index'>
          <a href="#">Women</a>
      </li>-->
      <router-link v-for='(category, index) in catData' key='index' :to="{ name: 'CategoryView', params: { catname: category.category_name.toLowerCase(), catId: category.id } }"  tag='li'>
          <a href="#">{{ category.category_name }}</a>
          </router-link>
  </ul>
</div>
</aside> <!-- end sidebar -->
</template>

<script>
    export default {
        props: ['catData'],
        name: 'Category',
        mounted () {
            console.log('category is here');
        }
    }
</script>



