<template>
    <!-- Pagination -->
    <div class="pagination-wrap">
        <p class="result-count">Showing: {{ curr }} of {{ properties.count }} results</p>              
        <nav class="pagination right clear">
            <a @click='prev'>
                <i class="fa fa-angle-left"></i>
            </a>              
            <a @click='next'>
                <i class="fa fa-angle-right"></i>
            </a>
        </nav>
    </div>
</template>

<script type="text/javascript">
    export default {
        props: ['properties'],
        data () {
            return {
                counter: 0,
            }
        },
        methods: {
            next () {
                this.$emit('paginate', 12);
            },
            prev () {
                this.$emit('paginate', 0);
            }
        },
        computed: {
            curr () {
                if (this.counter === 0) {
                    return this.properties.current_count;
                }
            }
        }
    }
</script>



