<template>
  <div>
    <!-- Page Title -->
    <section class="page-title text-center">
      <div class="container relative clearfix">
        <div class="title-holder">
          <div class="title-text">
            <h1 class="uppercase">Shopping Cart</h1>
          </div>
        </div>
      </div>
    </section> <!-- end page title -->

    <!-- Cart -->
    <section class="section-wrap shopping-cart pt-0">
      <div class="container relative">
        <div class="row">
          <div class="col-md-12">
            <div class="table-wrap mb-30">
              <table class="shop_table cart table">
                <thead>
                  <tr>
                    <th class="product-name" colspan="2">Product</th>
                    <th class="product-price">Price</th>
                    <!--<th class="product-quantity">Quantity</th>-->
                    <th class="product-subtotal">Total</th>
                  </tr>
                </thead>
                <tbody>
                  <cart-item v-for='(item, index) in cart' key='index' :cartItem='item' :index='index'></cart-item>
                </tbody>
              </table>
            </div>
            <div class="row mb-50">
              <div class="col-md-5 col-sm-12">
                <div class="cart_totals">
                  <h2 class="heading relative heading-small uppercase mb-30">Cart Totals</h2>

                  <table class="table shop_table">
                    <tbody>
                      <tr class="cart-subtotal">
                        <th>Cart Subtotal</th>
                        <td>
                          <span class="amount">GH&#8373; {{ getTotal }}</span>
                        </td>
                      </tr>
                      <tr class="shipping">
                        <th>Shipping</th>
                        <td>
                          <span>Free Shipping</span>
                        </td>
                      </tr>
                      <tr class="order-total">
                        <th><strong>Order Total</strong></th>
                        <td>
                          <strong><span class="amount">GH&#8373; {{ getTotal }}</span></strong>
                        </td>
                      </tr>
                    </tbody>
                  </table>


                </div>
              </div>
              <div class="col-md-7">
                <div class="actions right">
                  <div class="wc-proceed-to-checkout">
                    <router-link to='/checkout'>
                    <a class="btn btn-md btn-color"><span class="text-weight">proceed to checkout</span></a>
                    </router-link>
                  </div>
                </div>
              </div>

            </div>
          </div>
        </div>
      </div>
    </section>

  </div>
</template>

<script type="text/javascript">
  import CartItem from './CartItem'

export default {
  store: ['cart'],
  components: {
    CartItem
  },
  computed: {
    getTotal() {
      let total = 0;
      this.cart.map((v, i) => {
        total += (v.price * v.quantity);
      })
        return total;
    }
  }
}
</script>

<style type="text/css" scope>
.text-weight {
  font-weight: bold;
}

</style>



