<template>
<div class="col-md-4 col-xs-6" >
    <div class="product-item">
        <div class="product-img">
          <a href="#">
              <img :src='product.image_url' alt="">
          </a>
         <!-- <div class="product-label">
              <span class="sale">sale</span>
         </div>-->
          <div class="product-actions">
              <a href="#" class="product-add-to-wishlist" data-toggle="tooltip" data-placement="bottom" title="Add to wishlist">
                  <i class="fa fa-heart"></i>
              </a>                    
          </div>
          <a href="#" class="product-quickview">Quick View</a>
        </div>

        <div class="product-details">
            <router-link :to="{ name: 'ViewProduct', params: { productId: product.id, productData: product } }" tag='h3'>
                    <a class='product-title'>{{ product.name }}</a>
                    </router-link>
            <span class="price">
                <del v-if='product.price !== oldprice'>
                    <span> {{ product.price | currency('GH&#8373;') }}</span>
                </del>
                <ins>
                    <span class="ammount"> {{ oldprice | currency('GH&#8373;') }}</span>
                </ins>
            </span>
        </div>                  
    </div>
</div>
</template>

<script type="text/javascript">
    export default {
        props: ['product'],
        computed: {
            oldprice () {
                console.log(this.product)
                let discount = '';
                discount = this.product.price * (1 - (this.product.discount / 100));
                return discount;
            }
        }
    }
</script>



