<template>
<div id="header">
<!-- Navigation -->
<header class="nav-type-1">

<div class="top-bar hidden-sm hidden-xs">
<div class="container">
<div class="top-bar-line">
<div class="row">
<div class="top-bar-links">
<ul class="col-sm-6 top-bar-acc">
<li class="top-bar-link"><a href="#contact">Contact</a></li>
</ul>

</div>
</div>
</div>

</div>
</div> <!-- end top bar -->

<nav class="navbar navbar-static-top">
<div class="navigation" id="sticky-nav">
<div class="container relative">

<div class="row">

<div class="navbar-header">
<button type="button" class="navbar-toggle" data-toggle="collapse" data-target="#navbar-collapse">
<span class="sr-only">Toggle navigation</span>
<span class="icon-bar"></span>
<span class="icon-bar"></span>
<span class="icon-bar"></span>
</button>
<!-- Mobile cart -->
<div class="nav-cart mobile-cart hidden-lg hidden-md">
<div class="nav-cart-outer">
<div class="nav-cart-inner">
<a href="#" class="nav-cart-icon">{{ cartCount }}</a>
</div>
</div>
</div>
</div> <!-- end navbar-header -->

<div class="header-wrap">
<div class="header-wrap-holder">

<!-- Search -->
<div class="nav-search hidden-sm hidden-xs">
<form method="get">
<input type="search" class="form-control" placeholder="Search">
<button type="submit" class="search-button">
<i class="icon icon_search"></i>
</button>
</form>
</div>

<!-- Logo -->
<div class="logo-container">
<div class="logo-wrap text-center">
<a href="index.html">
<img class="logo" src="../assets/img/logo_dark.png" alt="logo">
</a>
</div>
</div>

<!-- Cart -->
<div class="nav-cart-wrap hidden-sm hidden-xs">
<div class="nav-cart right">
<div class="nav-cart-outer">
<div class="nav-cart-inner">
<router-link to='/cart'>
<a class='nav-cart-icon'>{{ cartCount }}</a>
</router-link>
</div>
</div>
</div>
<div class="menu-cart-amount right">
<span>
Cart /
</span>
</div>
</div> <!-- end cart -->

</div>
</div> <!-- end header wrap -->

<div class="nav-wrap">
<div class="collapse navbar-collapse" id="navbar-collapse">

<ul class="nav navbar-nav">

<li id="mobile-search" class="hidden-lg hidden-md">
<form method="get" class="mobile-search relative">
<input type="search" class="form-control" placeholder="Search...">
<button type="submit" class="search-button">
<i class="icon icon_search"></i>
</button>
</form>
</li>

<li class="dropdown">
<router-link to='/'>Home</router-link>
</li>

<li class="dropdown">
<router-link to='/checkout'>Checkout</router-link>
</li>

<li class="dropdown">
<router-link to='/cart'>View Cart</router-link>
</li>

</ul> <!-- end menu -->
</div> <!-- end collapse -->
</div> <!-- end col -->

</div> <!-- end row -->
</div> <!-- end container -->
</div> <!-- end navigation -->
</nav> <!-- end navbar -->
</header> <!-- end navigation -->
</div>
</template>

<script>
export default {
    store: ['cart', 'totalPrice'],
    computed: {
       cartCount () {
           return this.cart.length;
       }
   }
}
</script>



