<template>
  <div>
    <!-- Page Title -->
    <section class="page-title text-center">
      <div class="container relative clearfix">
        <div class="title-holder">
          <div class="title-text">
            <h1 class="uppercase">Checkout</h1>
          </div>
        </div>
      </div>
    </section> <!-- end page title -->

    <!-- Checkout -->
    <section class="section-wrap checkout pt-0 pb-50">
      <div class="container relative">
        <div class="alert alert-success" v-show='alert'>
          <strong>Success!</strong> Your order has been placed. <router-link to='/orders'>View Order</router-link>
</div>
        <div class="row">

          <div class="ecommerce col-xs-12">

            <form name="checkout" class="checkout ecommerce-checkout row" @submit.prevent='submitForm'>

              <div class="col-md-8" id="customer_details">
                <div>
                  <h2 class="heading uppercase mb-30">billing address</h2>

                  <p class="form-row form-row-wide address-field update_totals_on_change validate-required ecommerce-validated" id="billing_country_field">
                    <label for="billing_country">Country
                      <abbr class="required" title="required">*</abbr>
                    </label>
                    <select name="billing_country" id="billing_country" class="country_to_state country_select" title="Country *">
                      <option value="GH">Ghana</option>
                    </select>
                  </p>

                  <p class="form-row form-row-last validate-required ecommerce-invalid ecommerce-invalid-required-field" id="billing_last_name_field">
                    <label for="billing_last_name">Full Name
                      <abbr class="required" title="required">*</abbr>
                    </label>
                    <input type="text" class="input-text" placeholder v-model='name' id="billing_last_name">
                  </p>

                  <p class="form-row form-row-wide address-field validate-required ecommerce-invalid ecommerce-invalid-required-field" id="billing_address_1_field">
                    <label for="billing_address_1">Address
                      <abbr class="required" title="required">*</abbr>
                    </label>
                    <input type="text" class="input-text" placeholder="Street address" v-model='address' id="billing_address_1">
                  </p>

                  <p class="form-row form-row-wide address-field" id="billing_address_2_field">
                    <input type="text" class="input-text" placeholder="Landmark (optional)" v-model='landmark' id="billing_address_2">
                  </p>

                  <p class="form-row form-row-wide address-field validate-required" id="billing_city_field" data-o_class="form-row form-row-wide address-field validate-required">
                  <label for="billing_city">Town / City
                    <abbr class="required" title="required">*</abbr>
                  </label>
                  <input type="text" class="input-text" placeholder="Town / City" v-model='city' id="billing_city">
                  </p>

                  <p class="form-row form-row-first validate-required validate-email" id="billing_email_field">
                  <label for="billing_email">Email Address
                    <abbr class="required" title="required">*</abbr>
                  </label>
                  <input type="text" class="input-text" placeholder v-model='email' id="billing_email">
                  </p>

                  <p class="form-row form-row-last validate-required validate-phone" id="billing_phone_field">
                  <label for="billing_phone">Phone
                    <abbr class="required" title="required">*</abbr>
                  </label>
                  <input type="text" class="input-text" placeholder v-model='phone' id="billing_phone">
                  </p>

                  <div class="clear"></div>

                </div>

                <div class="clear"></div>

                <div>
                  <p class="form-row notes ecommerce-validated" id="order_comments_field">
                  <label for="order_comments">Order Notes</label>
                  <textarea name="order_comments" class="input-text" id="order_comments" v-model='notes' placeholder="Notes about your order, e.g. special notes for delivery." rows="2" cols="5"></textarea>
                  </p>
                </div>

                <div class="clear"></div>

              </div> <!-- end col -->


              <div class="col-md-4">
                <div class="order-review-wrap ecommerce-checkout-review-order" id="order_review">
                  <h2 class="heading uppercase mb-30">Your Order</h2>
                  <table class="table shop_table ecommerce-checkout-review-order-table">
                    <tbody>
                      <tr v-for='(item, index) in cart'>
                        <th>{{ item.name }}<span class="count"> x {{ item.quantity }}</span></th>
                        <td>
                          <span class="amount">GH&#8373; {{ item.quantity * item.price }}</span>
                        </td>
                      </tr>
                      
                      <tr class="cart-subtotal">
                        <th>Cart Subtotal</th>
                        <td>
                          <span class="amount">GH&#8373; {{ subTotal }}</span>
                        </td>
                      </tr>
                      <tr class="shipping">
                        <th>Shipping</th>
                        <td>
                          <span>Free Shipping</span>
                        </td>
                      </tr>
                      <tr class="order-total">
                        <th><strong>Order Total</strong></th>
                        <td>
                          <strong><span class="amount">GH&#8373; {{ subTotal }}</span></strong>
                        </td>
                      </tr>
                    </tbody>
                  </table>

                  <div id="payment" class="ecommerce-checkout-payment">
                    <h2 class="heading uppercase mb-30">payment method</h2>
                    <ul class="payment_methods methods">

                      <li class="payment_method_bacs">
                        <input id="payment_method_bacs" type="radio" class="input-radio" name="payment_method" value="Pay Now" v-model='picked'>
                        <label for="payment_method_bacs">Pay Now</label>
                        <div class="payment_box payment_method_bacs">
                          <p>Make your payment directly into our bank account via the provided payment gateway. Please use your Order ID as the payment reference. Your order wont be shipped until the funds have cleared in our account.</p>
                        </div>
                      </li>

                      <li class="payment_method_cheque">
                        <input id="payment_method_cheque" type="radio" class="input-radio" name="payment_method" value="Pay on Delivery" v-model='picked'>
                        <label for="payment_method_cheque">Pay on Delivery</label>
                        <div class="payment_box payment_method_cheque">
                          <p>Make payments when the product is delivered the provided address before handover.</p>
                        </div>
                      </li>

                    </ul>
                    <div class="form-row place-order">
                      <input type="submit" name="ecommerce_checkout_place_order" class="btn btn-lg" id="place_order" value="Place order" @click='submitForm'>
                    </div>
                  </div>
                </div>
              </div> <!-- end order review -->
            </form>

          </div> <!-- end ecommerce -->

        </div> <!-- end row -->
      </div> <!-- end container -->
    </section> <!-- end checkout -->
    </div>
</template>

<script type="text/javascript">
  import Devless from '../utils/devless'

  export default {
    store: ['cart'],
    data() {
      return {
        alert: false,
        name: '',
        address: '',
        landmark: '',
        city: '',
        email: '',
        phone: '',
        notes: '',
        picked: 'Pay Now'
      }
    },
    computed: {
      subTotal () {
        let total = 0;
        this.cart.map((v) => {
          total += v.price * v.quantity;
        });
        return total;
      }
    },
    methods: {
      submitForm () {
        const data = {
          name: this.name,
          address: this.address,
          landmark: this.landmark,
          city: this.city,
          email: this.email,
          phone: this.phone,
          notes: this.notes,
          picked: this.picked,
          cart: this.cart
        };
        localStorage.setItem('order', JSON.stringify(data));
        this.alert = true;
        this.$route.go('/orders');
      }
    }
  }
</script>



