<template>
  <!-- Single Product -->
  <section class="section-wrap single-product">
    <div class="container relative">
      <div class="row">

        <div class="col-sm-6 col-xs-12 mb-60">

          <div class="flickity flickity-slider-wrap mfp-hover" id="gallery-main">

            <div class="gallery-cell">
              <a class="lightbox-img">
                <img :src='productData.image_url' alt="" />
              </a>
            </div>
          </div> <!-- end gallery main -->

        </div> <!-- end col img slider -->

        <div class="col-sm-6 col-xs-12 product-description-wrap">
          <h1 class="product-title">{{ productData.name }}</h1>
          <span class="price">
           <!-- <del>
              <span>$1550.00</span>
           </del>-->
            <ins>
              <span class="ammount">{{ productData.price | currency }}</span>
            </ins>
          </span>
          <p class="product-description">{{ productData.description }}</p>

          <ul class="product-actions clearfix">

            <li>
              <a href="#" class="btn btn-color btn-lg add-to-cart left" @click='addToCart(productData)'><span>Add to Cart</span></a>
            </li>
            <li>
              <div class="icon-add-to-wishlist left">
                <a href="#"><i class="icon icon_heart_alt"></i></a>
              </div>
            </li>
            <li>
              <div class="quantity buttons_added">
                <input type="button" value="-" class="minus" @click='decreaseQtn'/>
                <input type="number" title="Qty" class="input-text qty text" v-model='quantity' />
                <input type="button" value="+" class="plus"  @click='increaseQtn'>
              </div>
            </li>
          </ul>

          <div class="product_meta">
            <span class="sku">SKU: <a href="#">{{ productData.sku_code }}</a></span>
          </div>

        </div> <!-- end col product description --> 
      </div> <!-- end row -->

    </div> <!-- end row -->


    </div> <!-- end container -->
  </section> <!-- end single product -->

</template>

<script type="text/javascript">
  import Devless from '../utils/devless'

export default {
  store: ['cart'],
  data () {
    return {
      productData: {},
      quantity: 1
    }
  },
  created () {
    this.productData = this.$route.params.productData;
  },
  methods: {
    increaseQtn () {
      this.quantity += 1
    },
    decreaseQtn () {
      if (this.quantity !== 1) {
        this.quantity -= 1
      }
    },
    addToCart (item) {
      item.quantity = this.quantity;
      this.cart.push(item);
      alert('Item added to cart');
    }
  },
}
</script>



