<template>
  <tr class="cart_item">
    <td class="product-thumbnail">
      <a href="#">
        <img :src="cartItem.image_url" alt="">
      </a>
    </td>
    <td class="product-name">
      <a href="#">Fashion Shorts</a>
          </td>
    <td class="product-price">
      <span class="amount">GH&#8373; {{ cartItem.price }}</span>
    </td>
    <!--<td class="product-quantity">
      <div class="quantity buttons_added">
        <input type="button" value="-" class="minus" @click='decreaseQtn(index)' /><input type="number" :value="cartItem.quantity" title="Qty" class="input-text qty text" /><input type="button" value="+" class="plus" @click='increaseQtn(index)'>
      </div>
    </td>-->
    <td class="product-subtotal">
      <span class="amount">GH&#8373; {{ cartItem.price * cartItem.quantity }}</span>
    </td>
    <td class="product-remove">
      <a href="#" class="remove" title="Remove this item" @click='remove(index)'>
        <i class="icon icon_close"></i>
      </a>
    </td>
  </tr>
</template>

<script type="text/javascript">
  //import * as cartLib from '../utils/cartscript'

  export default {
    store: ['cart'],
    props: ['cartItem', 'index'],
    methods: {
      remove(index){
        this.cart.splice(index, 1);
      },
      decreaseQtn (index) {
        let item = this.cart[index];
        if(item.quantity !== 1) item.quantity--;
      },
      increaseQtn (index) {
        let item = this.cart[index];
        item.quantity++;
      }
    }
  }
</script>



