<template>
    <!-- Catalogue -->
    <section class="section-wrap pt-70 pb-40 catalogue">
        <div class="container relative">
            <div class="row">
                <div class="col-md-9 catalogue-col right mb-50">
                    <banner></banner>
                    <div class="shop-catalogue grid-view left">
                        <div class="row row-10 items-grid">
                            <products v-for='(product, index) in products' key='index' :product='product'></products>
                        </div>
                    </div>
                    <div class="clearfix"></div>
                    <pagination :properties='properties' v-on:paginate='pagination'></pagination>
                </div>
                <category :catData='category'></category>
            </div>
        </div>
    </section>
</template>

<script type="text/javascript">
import Banner from './Banner'
import Category from './Category'
import Pagination from './Pagination'
import Products from './Products'
import Devless from '../utils/devless'

export default {
    name: 'Shop',
    data () {
        return {
            category: [],
            products: [],
            properties: {}
        }
    },
    components: {
        Banner,
        Category,
        Pagination,
        Products
    },
    methods: {
        async fetchData (val=0) {
            const routeId = this.$route.params.catId;
            let res = '';
            if (this.$route.path === '/') {
                res = await this.queryProduct({ size: 12, offset: val });
                return this.products = res;
            }

            res = await this.queryProduct({ where: `shopit_category_id,${routeId}`, size: 12 });
            return this.products = res;
        },
        async fetchCat () {
            let category = '';
            const catLocal = localStorage.getItem('categories');

            if (catLocal === null) {
                category = await Devless.queryData('shopit', 'category');
                localStorage.setItem('categories', JSON.stringify(category.payload.results));
                return this.category = category.payload.results;
            }
            return this.category = JSON.parse(catLocal);
        },
        async queryProduct(param) {
            const res = await Devless.queryData('shopit', 'inventory', param);
            this.properties = res.payload.properties;
            return res.payload.results;
        },
        pagination (val) {
            this.fetchData(val);
        }
    },
    created() {
        console.log('shop...loading');
        this.fetchData();
    },
    watch: {
        '$route': 'fetchData'
    },
    mounted () {
        this.fetchCat();
    }
}
</script>



